  flex-direction: column;
  margin-bottom: 30px; }
  @media (max-width: 1920px) {
    .account__programms-section-header {
      margin-bottom: calc(20px + 10 * ((100vw - 375px) / 1545)); } }
  @media (max-width: 767px) {
    .account__programms-section-header {
      flex-direction: row;
      justify-content: space-between;
      align-items: center; } }

.account__programms-section-title {
  font-weight: 600;
  font-size: 20px;
  text-transform: uppercase;
  margin: 0; }

.account__programms-section-add {
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
  color: #DF6435;
  margin-left: auto;
  text-align: right; }

.account__timetable {
  max-width: 868px; }
  .account__timetable .timetable__table {
    grid-template-columns: repeat(7, 124px);
    margin-top: 10px; }
  .account__timetable .timetable__table-column-wrapper {
    padding: 4px; }
  .account__timetable .timetable__wrapper {
    padding: 0;
    background-color: unset;
    margin: 0; }
  .account__timetable .timetable__header {
    justify-content: flex-start; }
  .account__timetable .timetable__navigation {
    margin-left: 60px; }
  .account__timetable .timetable__table-column-lesson {
    font-size: 12px; }
  .account__timetable .timetable__table-column-day-of-the-week {
    font-size: 14px; }
  @media (max-width: 1023px) {
    .account__timetable .timetable__navigation {
      display: none; } }

.purchase-history {
  overflow: hidden; }

.purchase-history__list-headers {
  display: grid;
  grid-template-columns: 1.3fr 2.7fr 2.1fr 1.2fr 1fr;
  gap: 10px;
  font-size: 12px;
  color: #757575;
  padding: 0 40px; }
  @media (max-width: 1023px) {
    .purchase-history__list-headers {
      display: none; } }

.purchase-history__list {
  list-style: none;
  padding: 0; }

.purchase-history__list-item {
  display: grid;
  grid-template-columns: 1.3fr 2.7fr 2.1fr 1.2fr 1fr;
  gap: 10px;
  background: #ffffff;
  padding: 25px 40px; }
  .purchase-history__list-item:nth-child(even) {
    background-color: unset; }
  .purchase-history__list-item:not(:last-child) {
    margin-bottom: 10px; }
  @media (max-width: 1023px) {
    .purchase-history__list-item {
      grid-template-columns: 2fr 4.6fr 2.6fr 1fr;
      gap: 20px 25px;
      padding: 25px 15px; } }
  @media (max-width: 1023px) {
    .purchase-history__list-item:nth-child(even) {
      background-color: #ffffff; } }

.purchase-history__item-title {
  display: none; }
  @media (max-width: 1023px) {
    .purchase-history__item-title {
      display: block;
      font-size: 12px;
      margin-bottom: 3px;
      color: #757575; } }

@media (max-width: 1023px) {
  .purchase-history__item-value {
    font-size: 14px; } }

@media (max-width: 1023px) {
  .purchase-history__item:nth-child(2) {
    grid-column: span 4;
    order: 4; } }

@media (max-width: 1023px) {
  .purchase-history__item:nth-child(3) br {
    display: none; } }

@media (max-width: 1023px) {
  .purchase-history__item:nth-child(3) {
    grid-column: span 4;
    order: 5; } }
